import BaseApiService from "../../BaseApiService";

// ---------------- Default path ------------------ //
const DEFAULT_PATH = "/custom/api/login";
const COMMON_PATH = "/custom/api";

class EmploymentDetailsService extends BaseApiService {
  // ------------------------- To get employment list ----------------------------- //
  getEmploymentDetails = (loanId: string, applicantId: string) => {
    const endPoint = DEFAULT_PATH + `/${loanId}/${applicantId}/employmentdetail?isActive=true`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // -------------------------- To create a new employment information --------------------------- //
  createEmploymentDetail = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/dde/employmentdetail`;
    return this.makePostRequestWithAuth(endPoint, payload);
  };

  // --------------------------- To update the existing employment information --------------------- //
  updateEmploymentDetail = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/dde/employmentdetail`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };


  searchAll = (search:string) => {
    const endPoint = COMMON_PATH + `/employment/detail/search?${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
}

export default new EmploymentDetailsService();
